
#down {
  position: absolute;
  bottom: 50px;
  left: calc(50% - 40px);
  background-color: transparent;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 80px;
  height: 80px;
  border-radius: 80px;
  padding: 15px 20px;
/*display inline-block*/
  cursor: pointer;
/*transition-property opacity, filter, transform*/
/*transition-duration 0.15s*/
/*transition-timing-function linear*/
  transition: opacity 0.15s linear, -webkit-filter 0.15s linear, -webkit-transform 0.43s ease-out;
  transition: opacity 0.15s linear, filter 0.15s linear, transform 0.43s ease-out;
  transition: opacity 0.15s linear, filter 0.15s linear, transform 0.43s ease-out, -webkit-filter 0.15s linear, -webkit-transform 0.43s ease-out;
  font: inherit;
  color: #fff;
  text-transform: none;
/*background-color transparent*/
  border: 1px solid #fff;
  margin: 0;
  overflow: hidden;
  outline: 0;
}
#down .angle-down {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}
@media (max-width: 1100px) {
#down {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    bottom: 0;
}
}
@media (max-width: 374px) {
#down {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    bottom: 0;
}
}


