
.partition-nav {
  width: 40px;
  border-collapse: collapse;
  cursor: pointer;
}
.partition-nav--dark {
  transition: border-color 0.5s ease-out;
  border: 1px solid #fff;
}
.partition-nav--light {
  transition: border-color 0.5s ease-out;
  border: 1px solid #142d64;
}
.partition-nav__row {
  width: 30%;
  position: relative;
}
.partition-nav__row:after {
  content: '';
  display: block;
  margin-top: 100%;
}
.partition-nav__content {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #fff;
  font-size: 12px;
}
.partition-nav__content--dark {
  color: #fff;
}
.partition-nav__content--light {
  color: #142d64;
}
.partition-nav__content--active--dark {
  transition: background-color 0.5s ease-out, color 0.5s ease-out;
  background-color: #fff;
  color: #000;
}
.partition-nav__content--active--light {
  transition: background-color 0.5s ease-out, color 0.5s ease-out;
  background-color: #142d64;
  color: #fff;
}
.partition-nav__content-top-left {
  transition: border-color 0.5s ease-out, background-color 0.5s ease-out, color 0.5s ease-out;
}
.partition-nav__content-top-left--dark {
  border-right: thin solid rgba(255,255,255,0.7);
  border-bottom: thin solid rgba(255,255,255,0.7);
}
.partition-nav__content-top-left--light {
  border-right: thin solid rgba(20,45,100,0.7);
  border-bottom: thin solid rgba(20,45,100,0.7);
}
.partition-nav__content-top-right {
  transition: border-color 0.5s ease-out, background-color 0.5s ease-out, color 0.5s ease-out;
}
.partition-nav__content-top-right--dark {
  border-left: thin solid rgba(255,255,255,0.7);
  border-bottom: thin solid rgba(255,255,255,0.7);
}
.partition-nav__content-top-right--light {
  border-left: thin solid rgba(20,45,100,0.7);
  border-bottom: thin solid rgba(20,45,100,0.7);
}
.partition-nav__content-bottom-left {
  transition: border-color 0.5s ease-out, background-color 0.5s ease-out, color 0.5s ease-out;
}
.partition-nav__content-bottom-left--dark {
  border-right: thin solid rgba(255,255,255,0.7);
  border-top: thin solid rgba(255,255,255,0.7);
}
.partition-nav__content-bottom-left--light {
  border-right: thin solid rgba(20,45,100,0.7);
  border-top: thin solid rgba(20,45,100,0.7);
}
.partition-nav__content-bottom-right {
  transition: border-color 0.5s ease-out, background-color 0.5s ease-out, color 0.5s ease-out;
}
.partition-nav__content-bottom-right--dark {
  border-left: thin solid rgba(255,255,255,0.7);
  border-top: thin solid rgba(255,255,255,0.7);
}
.partition-nav__content-bottom-right--light {
  border-left: thin solid rgba(20,45,100,0.7);
  border-top: thin solid rgba(20,45,100,0.7);
}


