    line-height: 1.25;
}
.section__description--space-around .section__title {
    font-size: calc(46px / 2.1);
}
.section__description--space-around .section__issue-title {
    font-size: calc(36px / 2.1);
}
.section__description--space-around .space-around .desktop-hide {
    line-height: calc(44px / 2.1);
}
}


